  }, [_vm._v("\n        花生PeA具有严谨的行事逻辑来组成“自我迭代”功能，其中的结构如下：\n\n        - 花生PeA（pea3nut）\n            - 花生（PeA_nut）\n                - 实现（nut）\n                - 方针（PeA）\n                - 梦想（Neap）\n                - @-信仰（3）-@（已被移除）\n            - 自我意识\n\n        [《花生的社交指导方针》](http://blog.pea3nut.com/e1098)是一个非常好的例子可以说明这个结构的运作：\n\n        > 首先是 *自我意识* 发现自己在学校的作风在放在职场不合适。\n        > 为什么不合适呢，因为原有的作风对 *梦想（Neap）* 不利\n        > 所以接下来要修改 *方针（PeA）*。由原本的“避免社交”变为“拥抱社交”。\n        > *方针（PeA）* 的修改会影响更上层的 *实现（nut）*，如原来的“为了避免无用社交，所以干脆避免所有社交”不再适用，更好的一个办法是“为了不错过有效社交，尽可能参与所有社交”。\n\n        上边的结构和行动流可以看到明显的一些特点：\n\n        - 底层驱动上层，上层为底层服务\n        - 底层的变动会导致上层轰塌，所以越底层越稳定，越上层则变动越频繁\n\n        而整个自我迭代的过程，其实是：由 *自我意识* 不断寻找对 *梦想（Neap）* 更优的 *方针（PeA）* 然后修改 *实现（nut）* 的过程。\n\n        ---\n\n        与此同时，也有相当一部分 *方针（PeA）* 不参与“自我迭代”，属于不可迭代的。而本页面，则主要介绍这些不可迭代的 *方针（PeA）* 都有哪些，以及它们为什么成为了不可迭代的。\n\n        而花生PeA真正的颜色，就是由这些 *梦想（Neap）* 和 *不可迭代的方针（PeA）* 组成的。\n    ")]), _vm._v(" "), _c('block-collapsible', {
    attrs: {
      "title": "不因世界而改变",
      "lang": "md",
      "folded": true
    }
  }, [_vm._v("\n        > 通常牢骚发完之后，对方就会回以“辛苦的又不止你一个”这样的套话。为什么大家辛苦我就非得辛苦不可啊？这有什么逻辑关系吗？\n        > —— 《我的青春恋爱喜剧果然有问题》第七册 P217\n\n        在我刚刚来北京工作时，每天需要乘坐地铁上下班。地铁很挤，挤到连想抬起一只胳膊都需要花很大的力气。路线很长，每天要在地铁花掉2个小时。2个小时，一天清醒时间的 12.5%。\n\n        说实话我不知道其他人是如何心安理得的在地铁上看剧和玩游戏的——在如此的环境，被如此的对待，用如此的时间。\n\n        我才不管其他人玩的有多么开心，我只知道，我不接受。\n\n        于是我开始了挣扎。\n\n        我尝试利用这每天清醒时间的12.5%。我开始买书拿到地铁上看，书体积太大不方便阅读，那就撕成页来读。灯光太昏暗看不清书上的小字，那就在休息日制作一些在地铁上也能看清的单词卡……\n\n        尽管依旧像被对待垃圾一样对待——2个小时的摇摇晃晃被挤来挤去，但是我在挣扎。\n\n        奋力挣扎，企图在北京地铁上夺回一些尊严。\n\n        在一个个寒冷的夜晚，月光下，有我宣誓的身影：\n\n        > 我不想勉强自己和不感兴趣的人打好关系\n        > 因为与其他人一致而感到安心，荒唐至极\n        > 我想抵抗理所当然出现在那里的人流\n        > 虽然无法完全抵抗\n        > 我和那些只认为自己特别的人不同\n        > 要成为，真正特别的人\n\n        我常常会在下班的归途中大声宣誓，向自己宣誓，向整个世界宣誓。\n\n        ---\n\n        - [丽奈：我想成为特别的人 | 写于北京实习3周之际](http://blog.pea3nut.com/e872)\n    ")]), _vm._v(" "), _c('block-collapsible', {
    attrs: {
      "title": "时间是敌人",
      "lang": "md",
      "folded": true
    }
  }, [_vm._v("\n        人类是一种线性生物。\n\n        心爱的狗狗死了，只会伤心一段时间。不久之后，又可以因为其他的事情而开怀大笑。而多年之后，也可以波澜不惊的提起此事。\n\n        时间可以冲淡一切，伤口也会愈合。\n\n        > 过去的就让他过去吧\n\n        这是大人们常说的一句话。但是这很奇怪啊难道不是吗？重要的事情因为时间而变得不再重要了！\n\n        我觉得不可理喻，无法接受，耻辱万分。我要扭转这个人类的本能。\n\n        记得我看过这样的一个故事：\n\n        > 少女A有一个妹妹，特别要好的妹妹——相互信任、相互依赖、相互照顾、相互体谅。简直是理想中的姐妹关系。\n        >\n        > 很不幸的是，一场交通事故带走了她的妹妹。\n        >\n        > 少女A悲痛欲绝，一幕幕的过往经历从少女A脑内放映——如果没有妹妹，未来会是怎样的呢？\n        >\n        > 尽管少女A非常痛苦，每天以泪洗面，但生活总归要进行下去。\n        >\n        > 随着时间的推移，少女A惊讶的发现自己没有那么伤心了。依旧可以在没有妹妹的情况下开怀大笑，依旧可以在没有妹妹的房间里倒头就睡。\n        >\n        > 少女A觉得自己重要的妹妹正在一步步的向自己离去。\n        >\n        > 于是，少女A拿着她妹妹生前使用的一条发带做了一个眼罩，遮住了自己的一只眼睛。\n        >\n        > > “每当我因为遮住一只眼睛而感到不便时，我就能感受到，我的妹妹就在我的身边”\n\n        花生在看到这个故事时，产生了极大的共鸣——因为花生也是这么做的。 大家熟知的“卧薪尝胆”中的越王勾践，也是这么做的。\n\n        可惜，大部分人没有这么做，轻易的让“眼前的东西”和“更容易获得的东西”，代替了“更重要的东西”。于是，一个个“梦想”变成了“曾经的梦想”，变成了“期望的生活状态”。\n\n        我要改变这个行为，不因为与生俱来而不去改变，不因为大家接受选择接受。\n\n        时间是敌人。\n    ")]), _vm._v(" "), _c('block-collapsible', {
