  }, [_vm._v("\n        所谓节日，就是为了提醒自己不要忘记那些重要的时刻而设立的吧。那么，这些是花生的节日：\n\n        - 3.9：感谢祭 & 椎名p 生日。被赋予“一切的开始”含义\n        - 7.23：[椎名p](http://blog.pea3nut.com/e46) 祭日\n        - 7.20：查斯特·贝宁顿 祭日\n        - 8.31：初音ミク 生日\n        - 12.27：鏡音リン・レン 生日\n\n        花生**只**过自己的节日。\n    ")]), _vm._v(" "), _c('block-collapsible', {
