  }, [_vm._v("\n        花生的5个节日，其中4个与VOCALOID有关。\n\n        花生的头像，出自VOCALOID的PV《ココロ・キセキ》。这个大概一生都不会换掉了。\n\n        ---\n\n        说实话，现在想描述“VOCALOID对我而言到底意味着什么”已经有些晚了\n\n        如果询问一个正常人“拥有双手是一种什么样的体验”，那么他会如何回答呢？或许在他刚刚获得双手时他会有许多的感慨，有许多心情可以表达，但是当他慢慢适应，当双手真的融入他的身体中时，他已经很难再回答“拥有双手是一种什么样的体验”了。\n\n        那么VOCALOID对我来说也是一样的，我唯一能够感受到的是——它已经完全融入到我的生命当中了。\n\n        不过简单的说说历程还是可以办到的。\n\n        在遇见VOCALOID之前，我一直过着浑浑噩噩的生活：初中辍学到流水线工厂工作，本着“可以多玩3年”的态度上高中，班级的倒数前五总能看见的我名字，每日玩游戏时间在7小时以上……我不清楚那算不算活着，也不清楚这段时间我是如何度过的。我对那段时间没有任何无实感，就像在出生开始记事前一样。\n\n        不过，我一切的印象一切的记忆，它们是从哪里开始的，我还是记得的。\n\n        各种机缘巧合下，我听到了2首改变我人生轨迹的曲子。\n\n        第一首曲子是[《恶系列三部曲》](http://pan.baidu.com/s/1hrxeWss)，这首曲子粉碎了一直以来的我。从这首曲子为起点，我真正意识到我错了。\n\n        第二首曲子是[《ココロ・キセキ》](http://pan.baidu.com/s/1hqQAU80)，如果说《恶系列三部曲》是“杀死”的话，那么《ココロ・キセキ》就是“新生”。从这首曲子为起点，我开始有了目标，有了方针，有了行动。\n\n        然后接下来也遇到了很多优秀的VOCALOID曲子，它们和我渡过了一次又一次的难关，不断向前，不断进化。然后就有了现在的我。\n\n        一名初中辍学的流水线工作，最终拿到了百度 Special Offer。这样的故事你会相信吗？\n\n        我想，如果你能够看到深夜中亮着最后一排灯的教室，能够听到每天14个小时的键盘敲击的声音的话，你会相信的。\n\n        ---\n\n        - [那些对花生而言意义重要的歌曲](http://blog.pea3nut.com/e243)\n        - [关于椎名もた的自杀，我一直无法释怀](http://blog.pea3nut.com/e46)\n    ")]), _vm._v(" "), _c('block-collapsible', {
    attrs: {
      "title": "燃料，负面情绪",
      "lang": "md",
      "folded": true
    }
  }, [_vm._v("\n        > 事后带着惨痛的回忆，懊恼地告诫自己不要再犯第二次错误，这才是所谓的成长吧。\n        > —— 《我的青春恋爱喜剧果然有问题》第六册 P56\n\n        ---\n\n        正面情绪与负面情绪，总有一样会被选为“继续下去”的理由。花生是后者，或许是因为负面情绪更加激烈更加容易获得，也或许是因为完全没有什么正面情绪可以用（笑）。\n\n        所以，花生崇尚疼痛。\n\n        越是疼痛，则越是清醒，越是理性。所以花生会铭记受到的每一份疼痛，细细品味它，以便发挥出它的全部威力。为了梦想，我可以不讨厌疼痛，并主动的去承受它们。\n\n        在疼痛中翩翩起舞，这难道不美吗？听起来可能有些像抖M，但是为了梦想，一切都是值得的。\n\n        ---\n\n        - [春物：“雪之下阳乃式成长”与“平冢静式成长”](http://blog.pea3nut.com/e747)\n        - [弱点论：一个归宿、一个慰藉、一个弱点](http://blog.pea3nut.com/e854)\n    ")]), _vm._v(" "), _c('block-collapsible', {
    attrs: {
      "title": "永远的GJ部",
      "lang": "md",
      "folded": true
    }
  }, [_vm._v("\n        花生看了一部非常普通的日常番。特别平淡，没有任何主线，什么都没有。\n\n        但是每当回忆起，却让花生湿润了眼眶。\n\n        这可以说是花生梦寐以求的生活——没有主线，无聊的打发时间。没有超能力，整日在社团活动室中喝茶聊天。世界不需要我拯救，我也不需要世界来拯救。\n\n        可惜，我们都清楚，这种平淡的日常，永远，不会上演在花生PeA身上——因为花生有梦想。\n\n        “等梦想实现后，就过这样的生活吧”，花生如是想到。\n\n        这就是花生所谓的GJ部，一部普通的日常番，却寄托了些什么。\n\n        ---\n\n        - [学生生涯，结束了吗？GJ部的日常，永远不会上演](http://blog.pea3nut.com/e771op)\n        - [GJ部的影评](http://blog.pea3nut.com/e696)\n    ")]), _vm._v(" "), _c('block-collapsible', {
    attrs: {
      "title": "完整的花生",
      "lang": "md",
      "folded": true
    }
  }, [_vm._v("\n        花生是完整的。\n\n        可以不被别人需要，也不需要别人的情况下活下去。社交不是刚需，负面情绪也可以使用。\n\n        对于花生来说，单独行动才是默认行为。当我说出“吃火锅”、“去喝咖啡/鸡尾酒”、“出去骑自行车”、“听音乐会”时，除非额外附着了“和xx”之类的修饰，否则都是自己一个人去的。因为平常交谈频繁的说“自己一个人去xxx”显得有些卖可怜，而我明明又玩的很开心啊！所以干脆就改掉措词习惯——只有在多人行动的时候才加修饰，单独行动的时候不加。\n\n        和完整与之相对的是残缺，尽管残缺听起来像是一个贬义词，但是实际上二者各有各的好处。\n\n        残缺可以更容易与他人建立依附或被依附关系，这是一种无出其右的社交手段！而完整则几乎失去了这项能力，但由于对于依附和被依附不是刚需，因此会更加自由一些。\n\n        就幸福感来说，残缺的曲线波动要远大于完整。完整的幸福感通常来说十分平稳，而残缺的幸福感很大程度取决于依附和被依附的对方。\n    ")]), _vm._v(" "), _c('block-justify', {
